<template>
  <div class="container">
    <div class="inner_container">
      <div class="table">
        <el-form :inline="true" ref="selectForm" label-width="70px" :model="listQuery.entity">
          <el-form-item label="">
            <el-input v-model="listQuery.entity.number" clearable placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item label="" v-has-permi="['manage:building:list']">
            <el-select @visible-change="visibleChange"
                       v-model="listQuery.entity.buildingId"
                       placeholder="" size="small" clearable filterable :loading="selectLoading">
              <el-option
                  v-for="item in buildingList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="listQuery.entity.isFull" clearable size="small">
              <el-option
                  label=""
                  :value="false">
              </el-option>
              <el-option
                  label=""
                  :value="true">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button style="font-size: 16px;"  type="primary" @click="select"> </el-button>
            <el-button style="font-size: 16px;"  type="warning" @click="reset"> </el-button>
            <el-button style="font-size: 16px;"  type="success" v-has-permi="['room:save']" @click="showDialog(undefined)"> </el-button>
          </el-form-item>
        </el-form>
        <el-table :data="pageInfo.list" v-loading="loading" height="410">
          <el-table-column label="">
            <template slot-scope="scope">{{ (listQuery.page - 1) * (listQuery.rows) + scope.$index + 1 }}</template>
          </el-table-column>
          <el-table-column prop="number" label=""></el-table-column>
          <el-table-column prop="building.name" label=""></el-table-column>
          <el-table-column prop="maxCapacity" label=""></el-table-column>
          <el-table-column prop="currentNum" label=""></el-table-column>
          <el-table-column label="" width="260" fixed="right">
            <template slot-scope="scope">
              <el-button style="font-size: 14px;"  type="primary" v-has-permi="['room:query']" @click="showDialog(scope.row.id)"> </el-button>
              <template>
                <el-popconfirm title="" @confirm="del(scope.row.id)">
                  <el-button style="margin-left: 8px;font-size: 12px;" v-has-permi="['room:delete']"
                             slot="reference"
                              type="danger"> 
                  </el-button>
                </el-popconfirm>
              </template>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="page">
        <el-pagination
            background
            :page-size="listQuery.rows"
            :page-count="pageInfo.pages"
            layout="-> ,prev, pager ,next, jumper, total, sizes"
            :total="pageInfo.total"
            :current-page.sync="pageInfo.pageNum"
            @current-change="handleCurrentChange"
            @size-change="handleSizeChange"
        >
        </el-pagination>
      </div>
      <div class="dialog">
        <el-dialog
            :title="dialogTitle"
            :visible.sync="dialogVisible"
            width="50%"
            @closed="handleClose">
          <el-form ref="saveOrUpdateForm" :rules="rules" :model="room" label-width="80px">
            <el-form-item label="" prop="number">
              <el-input v-model="room.number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="buildingId">
              <tree-select
                  v-model="room.buildingId"
                  :options="buildingTree"
                  :normalizer="normalizer"
                  :show-count="true"
                  placeholder="">
              </tree-select>
            </el-form-item>
            <el-form-item label="" prop="maxCapacity">
              <el-input-number v-model="room.maxCapacity"></el-input-number>
            </el-form-item>
          </el-form>
          <div v-if="studentList.length">
            <el-table :data="studentList">
              <el-table-column label="" prop="name"></el-table-column>
              <el-table-column label="" prop="number"></el-table-column>
              <el-table-column label="" prop="faculty.name"></el-table-column>
              <el-table-column label="" prop="phone"></el-table-column>
              <el-table-column label="" prop="registrationDate"></el-table-column>
              <el-table-column label="" width="100">
                <template slot-scope="scope">
                  <span style="color: #67C23A" v-if="!scope.row.isLeave"></span>
                  <span style="color: #909399" v-else></span>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <span slot="footer" class="dialog-footer">
              <el-button type="danger" @click="handleClose">  </el-button>
              <el-button type="primary" @click="saveOrUpdate"
                         v-has-permi="['room:update','room:save']">  </el-button>
            </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import {list, query, saveOrUpdate, del} from '@/api/room'
import {list as listBuilding, listAll} from "@/api/building";
import {list as listStudent} from '@/api/student'
import TreeSelect from '@riophae/vue-treeselect'
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

export default {
  name: "index",
  components: {
    TreeSelect
  },
  methods:{
    handleClose() {
      this.room = {}
      this.studentList = []
      this.$refs['saveOrUpdateForm'].resetFields()
      this.dialogVisible = false
    },
    saveOrUpdate() {
      const _this = this
      this.$refs['saveOrUpdateForm'].validate((valid) => {
        if (valid) {
          saveOrUpdate(this.room).then(res => {
            if (res.statusCode === 200) {
              this.$message({
                message: res.msg,
                type: 'success'
              });
              _this.handleClose()
              _this.listData()
            }
          })
        } else {
          this.$message({
            message: '',
            type: 'error'
          });
          return false;
        }
      })
    },
    async showDialog(id) {
      await this.getBuildingTree()
      if (id) {
        const {data} = await query(id)
        if (data) {
          await this.getStudentList(id)
          this.room = data
          this.dialogVisible = true
        }
      } else {
        this.dialogVisible = true
      }
    },
    async getStudentList(id) {
      const {data} = await listStudent({
        entity: {roomId : id},
        rows: 10,
        page: 1
      })
      this.studentList = data.list
    },
    async getBuildingTree() {
      const {data} = await listBuilding()
      this.buildingTree = data
    },
    async visibleChange(val) {
      if (val) {
        this.selectLoading = true
        const {data} = await listAll()
        this.buildingList = data
        this.selectLoading = false
      } else {
        this.buildingList = []
      }
    },
    del(id) {
      del(id).then(res => {
        if (res.statusCode === 200) {
          this.$message({
            message: res.msg,
            type: 'success'
          });
          this.listData()
        }
      })
    },
    select() {
      this.listQuery.page = 1
      this.listData()
    },
    async listData() {
      this.loading = true
      const {data} = await list(this.listQuery)
      this.pageInfo = data
      this.loading = false
    },
    handleSizeChange(val) {
      this.listQuery.rows = val
      this.listData()
    },
    handleCurrentChange(val) {
      this.listQuery.page = val
      this.listData()
    },
    reset() {
      this.listQuery.page = 1
      this.listQuery.entity = {}
      this.listData()
    },
    normalizer(node) {
      if (node.children && !node.children.length) {
        delete node.children;
      }
      return {
        id: node.id,
        label: node.name,
        children: node.children
      };
    }
  },
  data() {
    const validatePass = (rule, value, callback) => {
      if (value < 1) {
        callback(new Error('1'));
      } else {
        callback();
      }
    };
    return {
      studentList:[],
      listQuery: {
        page: 1,
        rows: 10,
        entity: {}
      },
      pageInfo: {},
      loading: false,
      dialogVisible: false,
      dialogTitle: '',
      room: {
        id: undefined,
        number: undefined,
        buildingId: undefined,
        maxCapacity: undefined,
        isFull: undefined
      },
      buildingTree: [],
      buildingList: [],
      selectLoading: false,
      rules: {
        number: [
          {required: true, message:'', trigger: 'blur'}
        ],
        buildingId: [
          {required: true, message: "", trigger: 'blur'}
        ],
        maxCapacity: [
          {required: true, message: '', trigger: 'blur'},
          {validator:validatePass, message: '1', trigger: 'blur'}
        ]
      }
    }
  },
  created() {
    this.listData()
  }
}
</script>

<style scoped>
.table {
  padding: 30px;
}
</style>
