// src/api/leaveApplication.js
import request from '@/utils/request'

// 
export function createApplication(data) {
    return request({
        url: '/leave-applications',
        method: 'post',
        data
    })
}

// 
export function submitApplication(id, data) { // id
    return request({
        url: `/leave-applications/${id}/submit`, // 
        method: 'put',
        data
    })
}

// 
export function updateApplication(id, data) { // id
    return request({
        url: `/leave-applications/${id}`, // 
        method: 'put',
        data
    })
}

// 
export function approveApplication(id) { // 
    return request({
        url: `/leave-applications/${id}/approve`,
        method: 'put'
    })
}

// 
export function getApplicationById(id) { // 
    return request({
        url: `/leave-applications/byApplyID/${id}`,
        method: 'get'
    })
}

// 
export function getByStudentNo(studentNo) { // 
    return request({
        url: '/leave-applications/student',
        method: 'get',
        params: { studentNo }
    })
}

// 
export function getPendingApplications() {
    return request({
        url: '/leave-applications/pending',
        method: 'get'
    })
}

export function getCounselorPendingApplications() {
    return request({
        url: '/leave-applications/counselor/pending',
        method: 'get'
    })
}

// 
export function getByStatus(status) { // 
    return request({
        url: '/leave-applications/status',
        method: 'get',
        params: { status }
    })
}

// 
export function deleteApplication(id) { // 
    return request({
        url: `/leave-applications/delete/${id}`,
        method: 'delete'
    })
}

// 
export function uploadFile(data) {
    return request({
        url: 'uploads',
        method: 'post',
        data,
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    })
}

export function approveByCounselor(id) {
    return request({
        url: `/leave-applications/${id}/counselor-approve`,  // 
        method: 'put'  // PUT
    })
}

// 
export function getByClassName(className) {
    return request({
        url: '/leave-applications/by-class',
        method: 'get',
        params: { className }
    })
}

export function getByStudentName(studentName) {
    return request({
        url: '/leave-applications/by-name',
        method: 'get',
        params: { studentName }
    })
}

export function getByStudentNoAndStatus(studentNo) {
    return request({
        url: '/leave-applications/by-studentno',
        method: 'get',
        params: { studentNo }
    })
}

export function getApprovedApplications() {
    return request({
        url: '/leave-applications/approved',
        method: 'get'
    })
}