<template>
  <div class="application-container">
    <!--  -->
    <el-button type="primary" @click="dialogVisible = true" class="action-btn">
      <i class="el-icon-plus"></i> 
    </el-button>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="90%" :before-close="handleClose"
      class="custom-dialog">
      <!--  -->
      <div class="header-bar">
        <span>{{ formData.id }}</span>
        <span>{{ statusMap[formData.status] }}</span>
        <el-radio-group v-model="formData.printType" class="print-control">
          <el-radio :label="'preview'"></el-radio>
          <el-radio :label="'edit'"></el-radio>
        </el-radio-group>
      </div>

      <!--  -->
      <el-form :model="formData" :rules="rules" ref="form" label-width="150px" class="main-form">
        <!--  -->
        <el-card class="info-card">
          <div slot="header" class="card-header">
            <i class="el-icon-user"></i> 
          </div>
          <el-row :gutter="20">
            <el-col :span="8">
              <el-form-item label="" prop="student_no">
                <el-input v-model="formData.student_no" :disabled="formData.status !== 'draft'"
                  @blur="validateStudentNo"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="" prop="studentName">
                <el-input v-model="formData.studentName"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="" prop="gender">
                <el-select v-model="formData.gender">
                  <el-option label="" value="male"></el-option>
                  <el-option label="" value="female"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="" prop="department">
            <el-input v-model="formData.department"></el-input>
          </el-form-item>
          <el-col :span="8">
            <el-form-item label="" prop="className">
              <el-input v-model="formData.className"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="phone">
              <el-input v-model="formData.phone"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="parentName">
              <el-input v-model="formData.parentName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="parentPhone">
              <el-input v-model="formData.parentPhone"></el-input>
            </el-form-item>
          </el-col>
        </el-card>

        <!--  -->
        <el-card class="info-card">
          <div slot="header" class="card-header">
            <i class="el-icon-location"></i> 
          </div>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="address">
                <el-input type="textarea" v-model="formData.address"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="emergencyContact">
                <el-input v-model="formData.emergency_contact"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="emergencyPhone">
                <el-input v-model="formData.emergencyPhone"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="" prop="timePeriod">
            <el-date-picker v-model="formData.time_period" type="daterange" range-separator="" start-placeholder=""
              end-placeholder=""></el-date-picker>
          </el-form-item>
          <el-col :span="24">
            <el-form-item label="" prop="reason">
              <el-input type="textarea" v-model="formData.reason"></el-input>
            </el-form-item>
          </el-col>
        </el-card>

        <!--  -->
        <!-- <el-card class="info-card">
          <div slot="header" class="card-header">
            <i class="el-icon-document"></i> 
          </div>
          <el-upload
            action="#"
            list-type="picture-card"
            :file-list="formData.attachments"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :before-upload="beforeUpload"
            :auto-upload="false"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <div slot="tip" class="el-upload__tip"></div>
        </el-card> -->

        <!--  -->
        <el-card class="info-card">
          <div slot="header" class="card-header">
            <i class="el-icon-s-custom"></i> 
          </div>

          <el-form-item label="" prop="parentOpinion">
            <el-input type="textarea" v-model="formData.parentOpinion" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="parentAgree">
            <el-select v-model="formData.parent_agree">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="" prop="counselorStatus">
            <el-select v-model="formData.counselorStatus">
              <el-option label="" value="pending"></el-option>
              <el-option label="" value="approved"></el-option>
              <el-option label="" value="rejected"></el-option>
            </el-select>
          </el-form-item>
                    <el-form-item label="" prop="college">
            <el-select v-model="formData.college">
              <el-option label="" value="pending"></el-option>
              <el-option label="" value="approved"></el-option>
              <el-option label="" value="rejected"></el-option>
            </el-select>
          </el-form-item> -->
        </el-card>

        <!--  -->
        <el-card class="signature-card">
          <div slot="header" class="card-header">
            <i class="el-icon-edit"></i> 
          </div>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="studentSignature">
                <el-input type="text" v-model="formData.studentSignature" placeholder=""
                  class="signature-input"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="parentSignature">
                <el-input type="text" v-model="formData.parentSignature" placeholder=""
                  class="signature-input"></el-input>
              </el-form-item>
            </el-col></el-row>
        </el-card>

        <!--  -->
        <el-alert title="" type="warning" show-icon :closable="false" class="notice-alert">
          <template #content>
            <ul class="notice-list">
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>
          </template>
        </el-alert>
      </el-form>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleSubmit" :loading="submitLoading">
          
        </el-button>
        <el-button @click="dialogVisible = false"></el-button>

      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {
    createApplication,
    updateApplication,
    uploadFile
  } from '@/api/leaveApplication'
  import { list } from '@/api/student'

  export default {
    data() {
      return {
        isEdit: false,
        dialogVisible: false,
        submitLoading: false,
        statusMap: {
          draft: '',
          submitted: '',
          approved: '',
          rejected: ''
        },
        formData: {
          id: null,
          student_no: '',
          student_name: '',  // 
          gender: 'male',
          department: '',
          class_name: '',    // 
          phone: '',
          parent_name: '',   // 
          parent_phone: '',  // 
          address: '',
          emergency_contact: '',  // 
          emergency_phone: '',    // 
          time_period: [],        // 
          attachments: [],
          parent_opinion: '',  // 
          parent_agree: '',  // ''
          counselor_status: '',
          college_status: '',
          student_signature: '',  // 
          parent_signature: '',    // 
          status: 'draft',
          reason: ''
        },
        rules: {
          student_no: [
            { required: true, message: '', trigger: 'blur' },
            { pattern: /^\d{11}$/, message: '11' }
          ],
          studentName: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 10, message: '2-10' }
          ],
          phone: [
            { required: true, message: '', trigger: 'blur' },
            { pattern: /^1[3-9]\d{9}$/, message: '' }
          ],
          parentPhone: [
            { required: true, message: '', trigger: 'blur' },
            { pattern: /^1[3-9]\d{9}$/, message: '' }
          ],
          address: [
            { required: true, message: '', trigger: 'blur' },
            { min: 10, message: '' }
          ],
          time_period: [
            { validator: this.validateDateRange, trigger: 'change' }
          ],
          parent_opinion: [
            { required: true, message: '', trigger: 'blur' }
          ],
          reason: [
            { required: true, message: '', trigger: 'blur' }
          ],
          emergencyPhone: [
            { required: true, message: '', trigger: 'blur' },
            { pattern: /^1[3-9]\d{9}$/, message: '' }
          ],
        }
      }
    },
    computed: {
      // 
      studentId: {
        get() { return this.formData.student_no },
        set(value) { this.formData.student_no = value }
      },
      studentName: {
        get() { return this.formData.student_name },
        set(value) { this.formData.student_name = value }
      },
      department: {
        get() { return this.formData.department },
        set(value) { this.formData.department = value }
      },
      parentContact: {
        get() { return this.formData.emergency_contact },
        set(value) { this.formData.emergency_contact = value }
      },
      className: {
        get() { return this.formData.class_name },
        set(value) { this.formData.class_name = value }
      },
      phone: {
        get() { return this.formData.phone },
        set(value) { this.formData.phone = value }
      },
      parentName: {
        get() { return this.formData.parent_name },
        set(value) { this.formData.parent_name = value }
      },
      parentPhone: {
        get() { return this.formData.parent_phone },
        set(value) { this.formData.parent_phone = value }
      }
    },
    methods: {

      handleClose(done) { //  done 
        if (this.formData.status === 'draft') {
          // 
          done();
        } else {
          // 
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            done(); // 
          }).catch(() => {
            //  done()
          });
        }
      },
      // 
      validateDateRange(rule, value, callback) {
        if (!value || value.length !== 2) {
          callback(new Error(''))
        } else if (value[0] > value[1]) {
          callback(new Error(''))
        } else {
          callback()
        }
      },
      async validateStudentNo() {
        if (!this.formData.student_no || this.formData.student_no.trim() === '') {
          return
        }

        try {
          const response = await list({ entity: { number: this.formData.student_no } })
          if (response.data.list.length === 0) {
            this.$message.error('')
            this.formData.student_no = ''  // 
          } else {
            const studentInfo = response.data.list[0]
            // 
            this.formData.studentName = studentInfo.name
            this.formData.className = studentInfo.faculty.name
            this.formData.phone = studentInfo.phone
            this.$message.success('')
          }
        } catch (error) {
          console.error(':', error)
          this.$message.error('')
        }
      },

      // 
      async handleAttachments() {
        if (this.formData.attachments.length === 0) return []

        try {
          const uploadPromises = this.formData.attachments.map(async file => {
            const formData = new FormData()
            formData.append('file', file.raw)
            formData.append('apply_id', this.formData.id)
            return await uploadFile(formData)
          })

          return await Promise.all(uploadPromises)
        } catch (error) {
          this.$message.error('')
          throw error
        }
      },

      // 
      resetForm() {
        this.formData = {
          id: null,
          student_no: '',
          student_name: '',
          gender: 'male',
          department: '',
          address: '',
          emergency_contact: '',
          emergency_phone: '',  // 
          time_period: [],
          attachments: [],
          parent_opinion: '',
          parent_agree: '',  // 
          counselor_status: '',
          college_status: '',
          student_signature: '',
          parent_signature: '',
          status: 'draft',
          reason: ''
        }
        this.$nextTick(() => {
          this.$refs.form.clearValidate()
        })
      },

      // 
      editApplication(row) {
        this.isEdit = true
        this.formData = { ...row }
        this.dialogVisible = true
      },

      // 
      async handleSubmit() {
        try {
          // 
          await this.$refs.form.validate()

          // student_no
          if (!this.formData.student_no || this.formData.student_no.trim() === '') {
            this.$message.error('')
            return
          }


          // 
          const attachments = await this.handleAttachments()

          // 
          const requestData = {
            applyId: this.formData.id,
            studentNo: this.formData.student_no,
            studentName: this.formData.student_name || this.formData.studentName,
            gender: this.formData.gender,
            department: this.formData.department,
            className: this.formData.class_name || this.formData.className,
            phone: this.formData.phone,
            parentName: this.formData.parent_name || this.formData.parentName,
            parentPhone: this.formData.parent_phone || this.formData.parentPhone,
            address: this.formData.address,
            startDate: this.formData.time_period[0] ? new Date(this.formData.time_period[0]).toISOString() : null,
            endDate: this.formData.time_period[1] ? new Date(this.formData.time_period[1]).toISOString() : null,
            reason: this.formData.reason.trim(),
            emergencyContact: this.formData.emergency_contact || this.formData.emergencyContact,
            emergencyPhone: this.formData.emergency_phone,  // formData
            parentOpinion: this.formData.parent_opinion || this.formData.parentOpinion,
            parentAgree: this.formData.parent_agree,  // 
            counselorStatus: this.formData.counselor_status,
            collegeStatus: this.formData.college_status,
            studentSignature: this.formData.student_signature || this.formData.studentSignature,
            parentSignature: this.formData.parent_signature || this.formData.parentSignature,
            status: this.isEdit ? this.formData.status : 'submitted',
            createTime: new Date(),
            updateTime: new Date(),
            idCardPath: this.formData.id_card_path,
            contractPath: this.formData.contract_path,
            otherFilePath: this.formData.other_file_path
          };

          // 
          console.log('Request Data:', requestData)

          // API
          if (this.isEdit) {
            await updateApplication(requestData)
            this.$message.success('')
          } else {
            const response = await createApplication(requestData)
            console.log('API Response:', response)
            this.$message.success('')
          }

          // 
          this.dialogVisible = false
          this.isEdit = false
          this.resetForm()
          this.$emit('refresh-list')

        } catch (error) {
          console.error(':', error)
          this.$message.error('' + (error.response?.data?.message || ''))
        } finally {
          this.submitLoading = false
        }
      }
    }
  }
</script>

<style scoped>
  .application-container {
    padding: 20px;
    background-color: #f5f7fa;
    min-height: calc(100vh - 40px);
  }

  .signature-input {
    width: 100%;
    min-height: 40px;
    border: 1px solid #dcdfe6;
    border-radius: 4px;
    padding: 8px;
    transition: border-color 0.2s;
  }

  .signature-input:focus {
    border-color: #409EFF;
  }

  .info-card {
    margin-bottom: 25px;
    border: none;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s;
  }

  .info-card:hover {
    box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.15);
  }
</style>