<template>
  <el-form :model="form" label-width="120px">
    <!--  -->
    <el-row :gutter="20">
      <el-col :span="8">
        <el-form-item label="" prop="school">
          <el-input v-model="form.school" placeholder="" :disabled="true" />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="" prop="department">
          <el-input v-model="form.department" />
        </el-form-item>
      </el-col>
    </el-row>

    <!--  -->
    <el-form-item label="">
      <el-radio-group v-model="selectMode">
        <el-radio-button label="cascade"></el-radio-button>
        <el-radio-button label="search"></el-radio-button>
      </el-radio-group>


    <el-cascader
    v-if="selectMode === 'cascade'"
    v-model="cascaderValue" 
    :options="facultyOptions"
    :props="cascaderProps"
    placeholder="///"
    filterable
    @change="handleCascaderChange" 
    />

      <!--  -->
      <el-select
        v-else
        v-model="form.classId"
        filterable
        placeholder=""
      >
        <el-option
          v-for="item in flatClasses"
          :key="item.id"
          :label="item.fullName"
          :value="item.id"
        />
      </el-select>
    </el-form-item>

    <!--  -->
    <el-form-item label="" prop="absentDate">
      <el-date-picker
        v-model="form.absentDate"
        type="date"
        value-format="yyyy-MM-dd"
      />
    </el-form-item>
    
    <el-form-item label="">
      <el-input v-model="form.name" placeholder="" />
      <el-radio-group v-model="form.gender">
        <el-radio :label="1"></el-radio>
        <el-radio :label="0"></el-radio>
      </el-radio-group>
      <br>
      <el-input-number v-model="form.age" :min="0" />
    </el-form-item>

        <!--  -->
    <el-row :gutter="20">
      <el-col :span="8">
        <el-form-item label="" prop="registrar">
          <el-input v-model="form.registrar" placeholder="" />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="" prop="registrarPhone">
          <el-input v-model="form.registrarPhone" placeholder="" />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="" prop="reportDate">
          <el-date-picker
            v-model="form.reportDate"
            type="date"
            value-format="yyyy-MM-dd"
          />
        </el-form-item>
      </el-col>
    </el-row>

    <!--  -->
    <el-row :gutter="20">
      <el-col :span="8">
        <el-form-item label="" prop="visitDate">
          <el-date-picker
            v-model="form.visitDate"
            type="date"
            value-format="yyyy-MM-dd"
          />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="" prop="hospital">
          <el-input v-model="form.hospital" />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="" prop="diagnosis">
          <el-input v-model="form.diagnosis" />
        </el-form-item>
      </el-col>
    </el-row>

    <!--  -->
    <el-form-item label="" prop="phone">
      <el-input v-model="form.phone" />
    </el-form-item>

    <el-form-item label="" prop="symptoms">
      <el-input type="textarea" v-model="form.symptoms" />
    </el-form-item>

    <el-row :gutter="20">
      <el-col :span="8">
        <el-form-item label="" prop="returnDate">
          <el-date-picker
            v-model="form.returnDate"
            type="date"
            value-format="yyyy-MM-dd"
          />
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="">
          <el-radio-group v-model="form.isCured">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="">
          <el-radio-group v-model="form.isInfectious">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-col>
    </el-row>

    <!--  -->
    <el-form-item>
      <el-button type="primary" @click="submitForm"></el-button>
    </el-form-item>

  </el-form>
</template>

<script>
import { list } from '@/api/faculty'
import { addAbsenceRecord } from '@/api/absenceRecord'

export default {
  data() {
    return {
      selectMode: 'cascade',
      cascaderValue: [],  // 
      form: {
        classId: null,
        school: '',
        department: '',
        name: '',
        gender: 1,
        age: null,
        absentDate: '',
        registrar: '',
        registrarPhone: '',
        reportDate: '',
        phone: '',
        symptoms: '',
        visitDate: '',
        hospital: '',
        diagnosis: '',
        returnDate: '',
        isCured: 0,
        isInfectious: 0
      },
      cascaderProps: {
        value: 'id',
        label: 'name',
        children: 'children',
        checkStrictly: true
      },
      facultyOptions: [],
      flatClasses: []
    }
  },
  async created() {
    const { data } = await list()
    this.facultyOptions = data
    this.flatClasses = this.flattenClasses(data)
  },
  methods: {

    handleCascaderChange(valueArr) {
    // ID
    this.form.classId = valueArr[valueArr.length - 1]
    },

    async submitForm() {
        try {
         // 
        const postData = {
        ...this.form,
        class_id: this.form.classId,
        registrar_phone: this.form.registrarPhone,
        visit_date: this.form.visitDate,
        report_date: this.form.reportDate,
        absent_date: this.form.absentDate,
        return_date: this.form.returnDate,
        is_cured: this.form.isCured,
        is_infectious: this.form.isInfectious
    }
        delete postData.classId          // class_id
        delete postData.registrarPhone   // registrar_phone
        delete postData.reportDate       // report_date
        delete postData.absentDate       // absent_date
        delete postData.returnDate       // return_date
        delete postData.isCured           // is_cured
        delete postData.isInfectious     // is_infectious
        // classId
        if (Array.isArray(this.form.classId)) {
        this.form.classId = this.form.classId[this.form.classId.length - 1]
        }
        console.log(':', postData) // 
            await addAbsenceRecord(postData)  // postDatathis.form
        this.$message.success('')
        this.resetForm()
        } catch (error) {
        this.$message.error('')
        }
    },
    resetForm() {
        this.form = {
        // ...
        class_id: null,
        school: '',
        department: '',
        name: '',
        gender: 1,
        age: null,
        absent_date: '',
        registrar: '',
        registrar_phone: '',
        report_date: '',
        phone: '',
        symptoms: '',
        visit_date: '',
        hospital: '',
        diagnosis: '',
        return_date: '',
        is_cured: 0,
        is_infectious: 0
        }
    },

    flattenClasses(tree) {
      const result = []
      const walk = (nodes, path = []) => {
        nodes.forEach(node => {
          const currentPath = [...path, node.name]
          if (node.children && node.children.length > 0) {
            walk(node.children, currentPath)
          } else {
            result.push({
              id: node.id,
              fullName: currentPath.join(' / ')
            })
          }
        })
      }
      walk(tree)
      return result
    }
  }
}
</script>
