<template>
  <div>
    <div class="container">
      <div class="inner_container">
        <div class="table">
          <el-form>
            <el-form-item>
              <el-button v-has-permi="['system:function:save']"
                  style="font-size: 16px;"  type="success" @click="showDialog(undefined)"> 
              </el-button>
            </el-form-item>

          </el-form>
          <el-table v-loading="loading"
                    :data="functionList"
                    :tree-props="{children: 'children'}"
                    row-key="id">
            <el-table-column label="" prop="name" width="210"></el-table-column>
            <el-table-column label="" width="80">
              <template slot-scope="scope">
                <i class="iconfont" :class="scope.row.icon"></i>
              </template>
            </el-table-column>
            <el-table-column label="" prop="orderNum" width="80"></el-table-column>
            <el-table-column label="" prop="permission"></el-table-column>
            <el-table-column label="" prop="component"></el-table-column>
            <el-table-column label="" prop="path"></el-table-column>
            <el-table-column label="" width="240" fixed="right">
              <template slot-scope="scope">
                <el-button v-has-permi="['system:function:query']"
                    style="font-size: 14px;"  type="primary" @click="showDialog(scope.row.id)"> 
                </el-button>
                <template>
                  <el-popconfirm title="" @confirm="del(scope.row.id)">
                    <el-button style="margin-left: 8px;font-size: 12px;" v-has-permi="['system:function:delete']"
                               slot="reference"  type="danger"> 
                    </el-button>
                  </el-popconfirm>
                </template>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="dialog">
          <el-dialog
              :title="dialogTitle"
              :visible.sync="dialogVisible"
              width="40%"
              @closed="handleClose"
          >
            <el-form ref="saveOrUpdateForm" :rules="rules" :model="form" label-width="80px">
              <el-form-item label="">
                <tree-select
                    v-model="form.parentId"
                    :options="functionList"
                    :normalizer="normalizer"
                    :show-count="true"
                    placeholder=""></tree-select>
              </el-form-item>
              <el-form-item label="">
                <el-radio-group v-model="form.menuType">
                  <el-radio label="M"></el-radio>
                  <el-radio label="C"></el-radio>
                  <el-radio label="F"></el-radio>
                </el-radio-group>
              </el-form-item>
              <el-row :gutter="20">
                <el-col :span="12">
                  <el-form-item label="" prop="name">
                    <el-input v-model="form.name" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" v-if="form.menuType !== 'F'">
                    <el-popover
                        placement="bottom-start"
                        width="460"
                        trigger="click"
                    >
                      <icon-select ref="iconSelect" @selected="select"/>
                      <el-input slot="reference" v-model="form.icon" placeholder="" readonly>
                        <i
                            v-if="form.icon"
                            slot="prefix"
                            :class="form.icon"
                            class="iconfont"
                            style="line-height: 40px"
                        />
                        <i v-else slot="prefix" class="el-icon-search el-input__icon"/>
                      </el-input>
                    </el-popover>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="">
                    <el-input-number v-model="form.orderNum" :min="0" :step="1"></el-input-number>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" prop="component" v-if="form.menuType === 'C'">
                    <el-input v-model="form.component" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" prop="path" v-if="form.menuType === 'C'">
                    <el-input v-model="form.path" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" v-if="form.menuType !== 'M'">
                    <el-input v-model="form.permission" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
                            <el-button type="danger" @click="handleClose">  </el-button>
                            <el-button type="primary" v-has-permi="['system:function:update']" @click="saveOrUpdate">  </el-button>
                          </span>
          </el-dialog>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {del, listTree, query, saveOrUpdate} from "@/api/system/SystemFunction";
import TreeSelect from '@riophae/vue-treeselect'
import IconSelect from '@/components/IconSelect'
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

export default {
  name: "index",
  components: {
    TreeSelect, IconSelect
  },
  data() {
    return {
      loading: true,
      functionList: [],
      form: {
        id: undefined,
        parentId: undefined,
        name: '',
        orderNum: undefined,
        path: '',
        component: '',
        menuType: 'M',
        permission: '',
        icon: ''
      },
      listTree: [],
      dialogTitle: '',
      dialogVisible: false,
      rules: {
        name: [
          {required: true, message: '', trigger: 'blur'}
        ],
        component: [
          {required: true, message: '', trigger: 'blur'}
        ],
        path: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    async listData() {
      this.loading = true
      const {data} = await listTree();
      this.functionList = data
      this.loading = false
    },
    async showDialog(id) {
      this.dialogTitle = ''
      if (id) {
        const {data} = await query(id)
        this.dialogTitle = ''
        if (data) {
          this.dialogVisible = true
          this.form = data
        }
      } else {
        this.dialogVisible = true
      }
    },
    del(id) {
      del(id).then(res => {
        if (res.statusCode === 200) {
          this.$message({
            message: res.msg,
            type: 'success'
          });
          this.listData()
        }
      })
    },
    saveOrUpdate() {
      const _this = this
      this.$refs['saveOrUpdateForm'].validate((valid) => {
        if (valid) {
          saveOrUpdate(this.form).then(res => {
            if (res.statusCode === 200) {
              this.$message({
                message: res.msg,
                type: 'success'
              });
              _this.handleClose()
              _this.listData()
            }
          })
        } else {
          this.$message({
            message: '',
            type: 'error'
          });
          return false;
        }
      })
    },
    select(name) {
      this.form.icon = name
    },
    handleClose() {
      this.form = {
        id: undefined,
        parentId: undefined,
        name: '',
        orderNum: undefined,
        path: '',
        component: '',
        menuType: 'M',
        permission: '',
        icon: ''
      }
      this.$refs['saveOrUpdateForm'].resetFields()
      this.dialogVisible = false
    },
    normalizer(node) {
      if (node.children && !node.children.length) {
        delete node.children;
      }
      return {
        id: node.id,
        label: node.name,
        children: node.children
      };
    }
  },
  created() {
    this.listData()
  }
}
</script>

<style scoped>
.table {
  padding: 30px;
}
</style>
