<template>
  <div class="router-container">
    <div class="container">
      <div class="inner_container">
        <div class="table">
          <el-form :inline="true" ref="form" :model="listQuery.entity">
            <el-form-item label="" v-has-permi="['room:list']">
              <el-select filterable clearable v-model="listQuery.entity.roomId" size="small"
                         placeholder="" @visible-change="getRoomList">
                <el-option v-for="item in roomList" :key="item.id" :label="item.number" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select clearable v-model="listQuery.entity.status" size="small"
                         placeholder="">
                <el-option label="" :value="true"></el-option>
                <el-option label="" :value="false"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker
                  value-format="yyyy-MM-dd"
                  v-model="listQuery.entity.createDate"
                  type="date" size="small"
                  placeholder="">
              </el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-button style="font-size: 16px;"  type="primary" @click="select"> </el-button>
              <el-button style="font-size: 16px;"  type="warning" @click="reset"> </el-button>
              <el-button style="font-size: 16px;"  type="success" v-has-permi="['repair:save']" @click="showDialog(undefined)"> </el-button>
            </el-form-item>
          </el-form>
          <el-table :data="pageInfo.list" v-loading="loading" height="405">
            <el-table-column prop="room.number" label="" width="150"></el-table-column>
            <el-table-column prop="describe" :show-tooltip-when-overflow="true" label="" width="200"></el-table-column>
            <el-table-column label="" prop="createDate"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <span style="color: #67C23A" v-if="scope.row.status"></span>
                <span style="color: #909399" v-else></span>
              </template>
            </el-table-column>
            <el-table-column label="" width="210">
              <template slot-scope="scope">
                <span v-if="scope.row.picture.length === 0"></span>
                <span v-else>
                  <el-button type="text" @click="viewPic(scope.row.picture)"></el-button>
                </span>
              </template>
            </el-table-column>
            <el-table-column label="" prop="finishDate"></el-table-column>
            <el-table-column label="" width="420" fixed="right">
              <template slot-scope="scope">
                <el-button type="primary" style="font-size: 14px;"  v-has-permi="['repair:query']" @click="showDialog(scope.row.id)"> </el-button>
                <el-button type="danger" style="font-size: 14px;"  v-has-permi="['repair:delete']" @click="del(scope.row.id)"> </el-button>
                <el-button type="success" style="font-size: 14px;" 
                           v-has-permi="['repair:update']"
                           @click="updateStatus(scope.row.id)"
                           :disabled="scope.row.status"
                > </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="page">
          <el-pagination
              background
              :page-size="listQuery.rows"
              :page-count="pageInfo.pages"
              layout="->, prev, pager, next, jumper, total, sizes"
              :total="pageInfo.total"
              :current-page.sync="pageInfo.pageNum"
              @current-change="handleCurrentChange"
              @size-change="handleSizeChange"
          >
          </el-pagination>
        </div>
        <div class="dialog">
          <el-dialog :title="dialogTitle"
                     width="45%"
                     :visible.sync="dialogVisible"
                     @closed="handleClose">

            <el-form :model="record" ref="saveOrUpdateForm" label-width="80px" :rules="rules">
              <el-form-item label="" prop="roomId">
                <el-select filterable clearable v-model="record.roomId" size="small"
                           placeholder="" @visible-change="getRoomList">
                  <el-option v-for="item in roomList" :key="item.id" :label="item.number" :value="item.id"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="describe">
                <el-input type="textarea" v-model="record.describe" rows="5" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <images-upload v-if="!destroy" :images="record.picture" @onChange="onPictureChange"></images-upload>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                            <el-button type="danger" @click="handleClose">  </el-button>
                            <el-button type="primary" @click="saveOrUpdate" :disabled="record.status">  </el-button>
                          </span>
          </el-dialog>


          <el-dialog title="" :visible.sync="pictureVisible" width="40%" @closed="handlePictureDialogClose">
            <el-image v-for="p in pictureList" :key="p"
                style="width: 100px; height: 100px; margin-left: 5px"
                :src="p"
                :preview-src-list="pictureList">
            </el-image>
          </el-dialog>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {list, query, saveOrUpdate, del, updateStatus} from "@/api/repair";
import {listAll as listRooms} from "@/api/room";
import imagesUpload from "./components/imagesUpload";
export default {
  name: "index",
  components: {
    imagesUpload
  },
  methods: {
    confirm(hook) {
      this.$confirm("", "", {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(hook)
    },
    updateStatus(id) {
      this.confirm(() => {
        updateStatus(id).then((res) => {
          if (res.statusCode === 200) {
            this.$message({
              type: 'success',
              message: res.msg
            });
            this.listData()
          }
        })
      })
    },
    del(id) {
      this.confirm(() => {
        del(id).then((res) => {
          if (res.statusCode === 200) {
            this.$message({
              type: 'success',
              message: res.msg
            });
            this.listData()
          }
        })
      })
    },
    saveOrUpdate() {
      const _this = this
      this.$refs['saveOrUpdateForm'].validate((valid) => {
        if (valid) {
          saveOrUpdate(this.record).then(res => {
            if (res.statusCode === 200) {
              this.$message({
                message: res.msg,
                type: 'success'
              });
              _this.handleClose()
              _this.listData()
            }
          })
        } else {
          this.$message({
            message: '',
            type: 'error'
          });
          return false;
        }
      })
    },
    onPictureChange(images) {
      this.record.picture = images
    },
    async showDialog(id) {
      this.dialogTitle = ''
      if (id) {
        const {data} = await query(id)
        this.dialogTitle = ''
        if (data) {
          this.record = data
          this.dialogVisible = true
        }
      } else {
        this.dialogVisible = true
      }
      this.destroy = false
      await this.getRoomList(true)
    },
    handleClose() {
      this.dialogVisible = false
      this.destroy = true
      this.roomList = []
      this.record = {
        id: undefined,
        roomId: undefined,
        room: undefined,
        describe: undefined,
        picture: undefined,
        status: undefined,
        createDate: undefined,
        finishDate: undefined
      }
      this.$refs['saveOrUpdateForm'].resetFields()
    },
    async listData() {
      this.loading = true
      const {data} = await list(this.listQuery)
      this.pageInfo = data
      this.loading = false
    },
    async getRoomList(v) {
      if (v) {
        const {data} = await listRooms()
        this.roomList = data
      } else {
        this.roomList = []
      }
    },
    select() {
      this.listQuery.page = 1
      this.listData()
    },
    reset() {
      this.roomList = []
      this.listQuery.entity = {}
      this.select()
    },
    viewPic(pic) {
      this.pictureList = []
      for (let p of pic) {
        this.pictureList.push(process.env.VUE_APP_IMG_PREFIX + p)
      }
      this.pictureVisible = true
    },
    handlePictureDialogClose() {
      this.pictureList = []
      this.pictureVisible = false
    },
    handleCurrentChange(val) {
      this.listQuery.page = val
      this.listData()
    },
    handleSizeChange(val) {
      this.listQuery.rows = val
      this.listData()
    },
  },
  data () {
    return {
      listQuery:{
        page: 1,
        rows: 10,
        entity: {}
      },
      pageInfo: {},
      loading: true,
      dialogVisible: false,
      dialogTitle: '',
      record:{
        id: undefined,
        roomId: undefined,
        room: undefined,
        describe: undefined,
        picture: undefined,
        status: undefined,
        createDate: undefined,
        finishDate: undefined
      },
      roomList: [],
      pictureList: [],
      pictureVisible: false,
      destroy: true, //
      rules: {
        roomId: [
          {required: true, message: '', trigger: 'blur'}
        ],
        describe: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    }
  },
  created() {
    this.listData()
  }
}
</script>

<style scoped>
.table {
  padding: 30px;
}
</style>
