<template>
    <div class="router-container">
        <div class="container">
            <div class="inner_container">
                <div class="table">
                    <el-form
                        :inline="true"
                        ref="form"
                        label-width="70px"
                        :model="listQuery.entity"
                    >
                        <el-form-item
                            label=""
                            v-has-permi="['student:list']"
                        >
                            <el-select
                                
                                v-model="listQuery.entity.studentId"
                                filterable
                                clearable
                                remote
                                placeholder=""
                                :remote-method="getStudentsFromRemote"
                                :loading="selectLoading"
                            >
                                <el-option
                                    v-for="item in students"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-select
                                
                                clearable
                                v-model="listQuery.entity.isBack"
                            >
                                <el-option label="" :value="true">
                                </el-option>
                                <el-option label="" :value="false">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-button
                                style="font-size: 16px"
                                type="primary"
                                @click="select"
                            >
                                </el-button
                            >
                            <el-button
                                style="font-size: 16px"
                                type="warning"
                                @click="reset"
                            >
                                </el-button
                            >
                            <el-button
                                style="font-size: 16px"
                                type="success"
                                v-has-permi="['leave:save']"
                                @click="showDialog(undefined)"
                            >
                                </el-button
                            >
                        </el-form-item>
                    </el-form>
                    <el-table
                        :data="pageInfo.list"
                        v-loading="loading"
                        height="405"
                    >
                        <el-table-column label="" width="80">
                            <template slot-scope="scope">
                                <el-popover
                                    placement="bottom"
                                    width="200"
                                    trigger="click"
                                    @show="getStudent(scope.row.student.id)"
                                >
                                    <el-form label-width="40px">
                                        <el-form-item label="">
                                            <el-input
                                                v-model="student.name"
                                                readonly
                                                
                                            ></el-input>
                                        </el-form-item>
                                        <el-form-item label="">
                                            <el-input
                                                v-model="student.number"
                                                readonly
                                                
                                            ></el-input>
                                        </el-form-item>
                                        <el-form-item label="">
                                            <el-input
                                                v-model="student.phone"
                                                readonly
                                                
                                            ></el-input>
                                        </el-form-item>
                                        <el-form-item
                                            label=""
                                            v-if="student.faculty"
                                        >
                                            <el-input
                                                v-model="student.faculty.name"
                                                readonly
                                                
                                            ></el-input>
                                        </el-form-item>
                                        <el-form-item
                                            label=""
                                            v-if="student.room"
                                        >
                                            <el-input
                                                v-model="student.room.number"
                                                readonly
                                                
                                            ></el-input>
                                        </el-form-item>
                                    </el-form>
                                    <span
                                        slot="reference"
                                        style="color: #409eff; cursor: pointer"
                                        >{{ scope.row.student.name }}</span
                                    >
                                </el-popover>
                            </template>
                        </el-table-column>
                        <el-table-column
                            prop="time"
                            label=""
                            width="160"
                        ></el-table-column>
                        <el-table-column
                            label=""
                            width="150"
                            prop="reason"
                            :show-overflow-tooltip="true"
                        ></el-table-column>
                        <el-table-column
                            prop="target"
                            label=""
                        ></el-table-column>
                        <el-table-column
                            prop="backDate"
                            width="100"
                            label=""
                        ></el-table-column>
                        <el-table-column label="" width="100">
                            <template slot-scope="scope">
                                <span
                                    style="color: #67c23a"
                                    v-if="scope.row.isBack"
                                    ></span
                                >
                                <span style="color: #909399" v-else>
                                    <span
                                        v-if="
                                            new Date() >
                                            new Date(
                                                scope.row.backDate + ' 23:59:59'
                                            )
                                        "
                                        style="color: #f56c6c"
                                        >()</span
                                    >
                                </span>
                            </template>
                        </el-table-column>
                        <el-table-column label="" width="400" fixed="right">
                            <template slot-scope="scope">
                                <el-button
                                    type="primary"
                                    style="font-size: 14px"
                                    v-has-permi="['leave:query']"
                                    @click="showDialog(scope.row.id)"
                                >
                                    </el-button
                                >
                                <el-button
                                    type="danger"
                                    style="font-size: 14px"
                                    v-has-permi="['leave:delete']"
                                    @click="del(scope.row.id)"
                                >
                                    </el-button
                                >
                                <el-button
                                    type="success"
                                    style="font-size: 14px"
                                    v-has-permi="['leave:update']"
                                    @click="update(scope.row.id)"
                                    :disabled="scope.row.back"
                                >
                                    </el-button
                                >
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="page">
                    <el-pagination
                        background
                        :page-size="listQuery.rows"
                        :page-count="pageInfo.pages"
                        layout="->, prev, pager, next, jumper, total, sizes"
                        :total="pageInfo.total"
                        :current-page.sync="pageInfo.pageNum"
                        @current-change="handleCurrentChange"
                        @size-change="handleSizeChange"
                    >
                    </el-pagination>
                </div>
                <div class="dialog">
                    <el-dialog
                        :title="dialogTitle"
                        :visible.sync="dialogVisible"
                        width="45%"
                        @closed="handleClose"
                    >
                        <el-form
                            :model="record"
                            ref="saveOrUpdateForm"
                            label-width="80px"
                            :rules="rules"
                        >
                            <template v-if="record && record.id !== undefined">
                                <el-form-item label="">
                                    <el-input
                                        v-model="record.student.name"
                                        readonly
                                    ></el-input>
                                </el-form-item>
                            </template>
                            <template v-else>
                                <el-form-item label="" prop="studentId">
                                    <el-select
                                        v-model="record.studentId"
                                        remote
                                        filterable
                                        placeholder=""
                                        :remote-method="getStudentsFromRemote"
                                        :loading="selectLoading"
                                    >
                                        <el-option
                                            v-for="item in students"
                                            :key="item.id"
                                            :label="item.name"
                                            :value="item.id"
                                        ></el-option>
                                    </el-select>
                                </el-form-item>
                            </template>
                            <template v-if="record && record.id !== undefined">
                                <el-form-item label="">
                                    <el-date-picker
                                        v-model="record.time"
                                        type="datetime"
                                        placeholder=""
                                        readonly
                                    >
                                    </el-date-picker>
                                </el-form-item>
                            </template>
                            <el-form-item label="" prop="backDate">
                                <el-date-picker
                                    v-model="record.backDate"
                                    type="date"
                                    value-format="yyyy-MM-dd"
                                    placeholder=""
                                >
                                </el-date-picker>
                            </el-form-item>
                            <el-form-item label="" prop="target">
                                <el-input
                                    v-model="record.target"
                                    placeholder=""
                                ></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="reason">
                                <el-input
                                    type="textarea"
                                    :rows="5"
                                    v-model="record.reason"
                                    placeholder=""
                                ></el-input>
                            </el-form-item>
                        </el-form>
                        <span slot="footer" class="dialog-footer">
                            <el-button type="danger" @click="handleClose">
                                 </el-button
                            >
                            <el-button
                                type="primary"
                                @click="saveOrUpdate"
                                v-has-permi="['leave:save', 'leave:update']"
                            >
                                 </el-button
                            >
                        </span>
                    </el-dialog>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { list, save, update, del, query } from "@/api/leave";
import { listByName, query as queryStudent } from "@/api/student";
export default {
    name: "index",
    methods: {
        update(id) {
            this.$confirm("", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            }).then(() => {
                update(id).then((res) => {
                    if (res.statusCode === 200) {
                        this.$message({
                            type: "success",
                            message: res.msg,
                        });
                        this.listData();
                    }
                });
            });
        },
        saveOrUpdate() {
            this.$refs["saveOrUpdateForm"].validate((valid) => {
                if (valid) {
                    save(this.record).then((res) => {
                        if (res.statusCode === 200) {
                            this.$message({
                                type: "success",
                                message: res.msg,
                            });
                            this.listData();
                            this.handleClose();
                        }
                    });
                } else {
                    this.$message({
                        message: "",
                        type: "error",
                    });
                    return false;
                }
            });
        },
        del(id) {
            this.$confirm("", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            }).then(() => {
                del(id).then((res) => {
                    if (res.statusCode === 200) {
                        this.$message({
                            type: "success",
                            message: res.msg,
                        });
                        this.listData();
                    }
                });
            });
        },
        async showDialog(id) {
            this.dialogTitle = "";
            if (id) {
                const { data } = await query(id);
                this.dialogTitle = "";
                if (data) {
                    this.record = data;
                    this.dialogVisible = true;
                }
            } else {
                this.dialogVisible = true;
            }
        },
        handleClose() {
            this.dialogVisible = false;
            this.students = [];
            this.record = {
                id: undefined,
                time: undefined,
                reason: undefined,
                isBack: undefined,
                studentId: undefined,
                target: undefined,
                backDate: undefined,
            };
            this.$refs["saveOrUpdateForm"].resetFields();
        },
        async getStudent(id) {
            const { data } = await queryStudent(id);
            this.student = data;
        },
        reset() {
            this.students = [];
            this.listQuery.entity = {};
            this.select();
        },
        select() {
            this.listQuery.page = 1;
            this.listData();
        },
        async listData() {
            this.loading = true;
            const { data } = await list(this.listQuery);
            this.pageInfo = data;
            this.loading = false;
        },
        handleSizeChange(val) {
            this.listQuery.rows = val;
            this.listData();
        },
        handleCurrentChange(val) {
            this.listQuery.page = val;
            this.listData();
        },
        async getStudentsFromRemote(query) {
            if (query !== "") {
                this.selectLoading = true;
                const { data } = await listByName(query);
                this.students = data.map((item) => {
                    return {
                        id: item.id,
                        name:
                            item.room.number +
                            " " +
                            item.name +
                            ":" +
                            item.number,
                    };
                });
                this.selectLoading = false;
            } else {
                this.students = [];
            }
        },
    },
    data() {
        return {
            listQuery: {
                page: 1,
                rows: 10,
                entity: {},
            },
            pageInfo: {},
            loading: true,
            selectLoading: true,
            students: [],
            student: {},
            record: {
                id: undefined,
                time: undefined,
                reason: undefined,
                isBack: undefined,
                studentId: undefined,
                target: undefined,
                backDate: undefined,
            },
            dialogVisible: false,
            dialogTitle: "",
            rules: {
                studentId: [
                    {
                        required: true,
                        message: "",
                        trigger: "change",
                    },
                ],
                reason: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                target: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                backDate: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    created() {
        this.listData();
    },
};
</script>

<style scoped>
.table {
    padding: 30px;
}
</style>
