<template>
  <div>
    <!--  -->
    <el-form :inline="true" :model="condition" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="condition.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="condition.absentDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
        <el-button @click="printTable"></el-button>
      </el-form-item>
      
      <!--  -->
      <el-form-item label="">
        <el-radio-group v-model="selectMode">
          <el-radio-button label="cascade"></el-radio-button>
          <el-radio-button label="search"></el-radio-button>
        </el-radio-group>

<el-cascader 
  v-if="selectMode === 'cascade'"
  v-model="cascaderValue" 
  :options="facultyOptions"
  :props="cascaderProps"
  @change="handleCascaderChange"
/>


        <el-select
          v-else
          v-model="condition.classId"
          filterable
          placeholder=""
        >
          <el-option
            v-for="item in flatClasses"
            :key="item.id"
            :label="item.fullName"
            :value="item.id"
          />
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="handleSearch"></el-button>
        <el-button @click="resetSearch"></el-button>
      </el-form-item>
    </el-form>

    <!--  -->
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="id" label="ID" width="60"></el-table-column>
      <el-table-column prop="name" label="" width="100"></el-table-column>
      <el-table-column label="" width="80">
        <template slot-scope="scope">
          {{ scope.row.gender === 1 ? '' : '' }}
        </template>
      </el-table-column>
      <el-table-column prop="age" label="" width="80"></el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">
          {{ getClassName(scope.row.class_id) }}
        </template>
      </el-table-column>
      <el-table-column prop="symptoms" label=""></el-table-column>
      <el-table-column prop="hospital" label=""></el-table-column>
      <el-table-column label="" width="120">
        <template slot-scope="scope">
          {{ formatDate(scope.row.absent_date) }}
        </template>
      </el-table-column>
      <el-table-column label="" width="120">
        <template slot-scope="scope">
          {{ formatDate(scope.row.return_date) }}
        </template>
      </el-table-column>
      
      <!--  -->
      <el-table-column label="" width="180">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible">
      <el-form :model="currentRecord">
        <el-form-item label="">
          <el-radio-group v-model="currentRecord.is_cured">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="currentRecord.is_infectious">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="dialogVisible = false"></el-button>
        <el-button type="primary" @click="confirmUpdate"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { 
  getAllRecords,
  searchRecords,
  deleteRecord,
  updateAbsenceRecord
} from '@/api/absenceRecord'
import { list } from '@/api/faculty'


export default {
  data() {
    return {
      tableData: [],
      condition: {
        name: '',
        absentDate: null,
        classId: null
      },
      dialogVisible: false,
      currentRecord: {},
      // 
      selectMode: 'cascade',
      facultyOptions: [],
      flatClasses: [],
      cascaderProps: {
        value: 'id',
        label: 'name',
        children: 'children',
        checkStrictly: true
      }
    }
  },
  async created() {
    await this.loadData()
    // 
    const { data } = await list()
    this.facultyOptions = data
    this.flatClasses = this.flattenClasses(data)
  },
  methods: {

printTable() {
      window.print();
    },

     handleCascaderChange(valueArr) {
    // ID
    this.condition.classId = valueArr.length > 0 ? valueArr[valueArr.length - 1] : null
  },

    // 
    async loadData() {
      try {
        const { data } = await getAllRecords()
        this.tableData = data
      } catch (error) {
        this.$message.error('')
      }
    },

    // 
    flattenClasses(tree) {
      const result = []
      const walk = (nodes, path = []) => {
        nodes.forEach(node => {
          const currentPath = [...path, node.name]
          if (node.children?.length) {
            walk(node.children, currentPath)
          } else {
            result.push({
              id: node.id,
              fullName: currentPath.join(' / ')
            })
          }
        })
      }
      walk(tree)
      return result
    },

    // 
    getClassName(classId) {
      const classInfo = this.flatClasses.find(item => item.id === classId)
      return classInfo ? classInfo.fullName : ''
    },

    // 
  formatDate(dateString) {
    if (!dateString) return ''
    const date = new Date(dateString)
    // Safari
    const pad = n => n.toString().padStart(2, '0')
    return `${date.getFullYear()}-${pad(date.getMonth() + 1)}-${pad(date.getDate())}`
  },

    // 
    async handleSearch() {
      try {
        // 
        const params = {
          name: this.condition.name,
        beginAbsentDate: this.condition.absentDate,
        endAbsentDate: this.condition.absentDate,
        classId: this.condition.classId // 
        }
        
        const { data } = await searchRecords(params)
        this.tableData = data
      } catch (error) {
        this.$message.error('')
      }
    },


    // 
    resetSearch() {
      this.condition = {
        name: '',
        absentDate: null,
        classId: null
      }
      this.loadData()
    },

    // 
    async handleDelete(index, row) {
      try {
        await deleteRecord(row.id)
        this.$message.success('')
        this.tableData.splice(index, 1)
      } catch (error) {
        this.$message.error('')
      }
    },

    // 
    handleEdit(index, row) {
      this.currentRecord = { ...row }
      this.dialogVisible = true
    },

    // 
    async confirmUpdate() {
      try {
        await updateAbsenceRecord(this.currentRecord)
        this.$message.success('')
        this.dialogVisible = false
        this.loadData()
      } catch (error) {
        this.$message.error('')
      }
    }
  }
}
</script>
