<template>
    <div class="app-container">
        <!--  -->
        <el-form :inline="true" class="search-form">
            <el-form-item label="">
                <el-input v-model="searchClass" placeholder="" clearable/>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="searchName" placeholder="" clearable/>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="searchStudentNo" placeholder="" clearable/>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="searchByClass"></el-button>
                <el-button type="success" @click="searchByName"></el-button>
                <el-button type="warning" @click="searchByStudentNo"></el-button>
                <el-button type="info" @click="searchApproved"></el-button>
            </el-form-item>
        </el-form>

        <el-table :data="applications" v-loading="loading">
            <el-table-column prop="studentName" label="" width="120"></el-table-column>
            <el-table-column prop="className" label="" width="150"></el-table-column>
            <el-table-column prop="department" label="" width="150"></el-table-column>
            <el-table-column prop="phone" label="" width="120"></el-table-column>
            <el-table-column prop="startDate" label="" width="120" :formatter="formatDate"></el-table-column>
            <el-table-column prop="endDate" label="" width="120" :formatter="formatDate"></el-table-column>
            <el-table-column prop="reason" label="" min-width="200"></el-table-column>
            <el-table-column prop="counselorStatus" label="" width="100">
                <template #default="scope">
                    <el-tag :type="scope.row.counselorStatus === '' ? 'warning' : 'success'">
                        {{ scope.row.counselorStatus }}
                    </el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" width="120">
                <template #default="scope">
                    <el-button type="primary" size="small" @click="handleApprove(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import { 
    getCounselorPendingApplications,
    approveByCounselor,
    getByClassName,
    getByStudentName,
    getByStudentNoAndStatus,
    getApprovedApplications
} from '@/api/leaveApplication'

export default {
    data() {
        return {
            applications: [],
            loading: false,
            searchClass: '',
            searchName: '',
            searchStudentNo: ''
        }
    },
    created() {
        this.fetchPendingApplications()
    },
    methods: {
        async fetchPendingApplications() {
            this.loading = true
            try {
                const res = await getCounselorPendingApplications()
                console.log(':', res)
                
                // 
                if (res?.statusCode === 200 && Array.isArray(res.data)) {
                    this.applications = res.data
                    console.log(':', this.applications)
                } else {
                    this.$message.error(res?.msg || '')  // msg
                }
            } catch (error) {
                console.error(':', error.response || error)
                this.$message.error(error.message || '')
            } finally {
                this.loading = false
            }
        },
        async handleApprove(row) {
            try {
                // API
                await approveByCounselor(row.applyId)
                this.$message.success('')
                // 
                await this.fetchPendingApplications()
            } catch (error) {
                console.error(':', error)
                // 
                this.$message.error(error.response?.data?.msg || '')
            }
        },
                async searchByClass() {
            if (!this.searchClass) {
                this.$message.warning('')
                return
            }
            await this.handleSearch(() => getByClassName(this.searchClass))
        },

        async searchByName() {
            if (!this.searchName) {
                this.$message.warning('')
                return
            }
            await this.handleSearch(() => getByStudentName(this.searchName))
        },

        async searchByStudentNo() {
            if (!this.searchStudentNo) {
                this.$message.warning('')
                return
            }
            await this.handleSearch(() => getByStudentNoAndStatus(this.searchStudentNo))
        },
                // 
        async handleSearch(apiCall) {
            this.loading = true
            try {
                const res = await apiCall()
                if (res?.statusCode === 200 && Array.isArray(res.data)) {
                    this.applications = res.data
                } else {
                    this.$message.error(res?.msg || '')
                }
            } catch (error) {
                this.$message.error(error.message || '')
            } finally {
                this.loading = false
            }
        },
    

        async searchApproved() {
            await this.handleSearch(getApprovedApplications)
        },
        formatDate(row, column, cellValue) {
            if (cellValue) {
                return new Date(cellValue).toLocaleDateString()
            }
            return ''
        }
    }
}
</script>

<style>
.el-table {
    margin-top: 20px;
    min-height: 400px;
}
</style>